.footer {
	/* Display & Box Model */
	background-color: $dark;
	&__top {
		/* Display & Box Model */
		display: flex;
		justify-content: center;
		align-items: center;
		height: 80px;
		margin: 0 calc(100% / 6);
		border-bottom: 1px solid $grey; }
	&__button {
		/* Display & Box Model */
		padding: 0;
		padding-right: 40px;
		border: none;
		background: transparent;

		/* Text */
		font-size: 12px;
		font-weight: bold;
		color: $light;
		text-transform: uppercase;

		/* Positioning */
		position: relative;

		/* Other */
		cursor: pointer;
		&::after {
			/* Display & Box Model */
			content: '';
			width: 12px;
			height: 12px;
			border-right: 2px solid $light;
			border-bottom: 2px solid $light;

			/* Positioning */
			position: absolute;
			right: 5%;
			top: 50%;
			transform: translateY(-70%) rotate(45deg); } }
	&__copyright {
		/* Display & Box Model */
		padding: 30px 0;
		margin: 0 calc(100% / 6);

		/* Text */
		font-size: 11px;
		font-weight: 500;
		color: $grey; } }
