.toolbar {
	/* Display & Box Model */
	width: 100%;
	height: 60px;
	background-color: $light;

	/* Positioning */
	position: fixed;
	top: 0;
	z-index: 9;
	&__menu {
		/* Display & Box Model */
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		padding: 0;
		margin: 0 250px; }
	&__link {
		/* Display & Box Model */
		padding: 5px 7px;
		margin: 0 20px;
		outline: none;

		/* Text */
		font-size: 12px;
		color: $dark;
		text-transform: uppercase;
		text-decoration: none;
		&:hover,
		&:focus {
			/* Display & Box Model */
			background: linear-gradient($light 0%, $light 50%, $green 50%, $green 100%); } }
	&__logo {
		/* Positioning */
		position: absolute;
		top: 50%;
		left: 100px;
		transform: translateY(-50%); } }
